export default "@import url(\"https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap\");\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity .5s ease;\n}\n.fade-enter-from,\n.fade-leave-active {\n  opacity: 0;\n}\n.widget {\n  display: flex;\n  box-sizing: border-box;\n  flex-direction: column;\n  position: relative;\n  background-color: #FFFFFF;\n  padding: 8px;\n  margin: 4px 8px;\n  border-radius: 4px;\n  text-align: center;\n}\n.widget--error {\n    border: 1px solid #f64545;\n}\n.widget--error:after {\n      content: 'Error';\n      color: #f64545;\n      font-size: 12px;\n      position: absolute;\n      bottom: 0;\n}\n.widget__delete {\n    position: absolute;\n    top: 0;\n    right: 0;\n    width: 24px;\n    padding: 8px;\n    box-sizing: content-box;\n}\n.widget__delete img {\n      width: 100%;\n}\n.widget__title {\n    width: 70%;\n    text-align: left;\n}\n.widget__subtitle {\n    margin: 8px auto 4px;\n    font-size: 12px;\n}\n.loader {\n  flex-grow: 1;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\nhtml, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font-size: 100%;\n  font: inherit;\n  vertical-align: baseline;\n}\n\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {\n  display: block;\n}\nbody {\n  line-height: 1;\n}\nol, ul {\n  list-style: none;\n}\nblockquote, q {\n  quotes: none;\n}\nblockquote:before, blockquote:after {\n  content: '';\n  content: none;\n}\nq:before, q:after {\n  content: '';\n  content: none;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\nbutton {\n  cursor: pointer;\n  border: none;\n  background-color: transparent;\n}\n.widget-main {\n  width: -webkit-fit-content;\n  width: -moz-fit-content;\n  width: fit-content;\n  max-width: 90vw;\n  font-family: 'Open Sans', sans-serif;\n  letter-spacing: .5px;\n  background-color: #93c2ff;\n  border-radius: 8px;\n  padding: 16px;\n  display: flex;\n  align-items: center;\n}\n.widget-main__placeholder {\n    margin-right: 12px;\n    font-size: 24px;\n    font-weight: bold;\n    color: #e3f0ff;\n}\n.widget-main__add {\n    position: relative;\n}\n.widget-main__button {\n    width: 48px;\n    height: 48px;\n    margin: 0 8px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: transparent;\n    border: 4px solid #e3f0ff;\n    border-radius: 4px;\n    font-size: 42px;\n    font-weight: bold;\n    color: #e3f0ff;\n    transition: all 0.3s ease-in-out;\n}\n.widget-main__button:hover {\n      color: #FFFFFF;\n      border-color: #FFFFFF;\n}\n.widget-main__content {\n    display: flex;\n    flex-wrap: wrap;\n}\n.draggable {\n  background: #fff;\n  padding: 10px;\n  margin: 10px;\n  border: 1px solid #ccc;\n  cursor: move;\n}\n.drag {\n  background: #f5f5f5;\n}\n.ghost {\n  opacity: 0.5;\n  background: #fff;\n}\n.fade-item {\n  display: inline-block;\n  margin-right: 10px;\n}\n.fade-enter-active, .fade-leave-active {\n  transition: all .5s;\n}\n.fade-enter, .fade-leave-to {\n  opacity: 0;\n  transform: translateY(30px);\n}\n.fadein-enter-active {\n  -webkit-animation: placeholder-animation 0.5s;\n          animation: placeholder-animation 0.5s;\n}\n.fadein-enter-active .inner {\n    transition-delay: 0.5s;\n}\n@-webkit-keyframes placeholder-animation {\nfrom {\n    opacity: 0;\n    transform: translateY(-80px);\n}\nto {\n    opacity: 1;\n    transform: translateY(0);\n}\n}\n@keyframes placeholder-animation {\nfrom {\n    opacity: 0;\n    transform: translateY(-80px);\n}\nto {\n    opacity: 1;\n    transform: translateY(0);\n}\n}\n"